.stack-list
    margin-left: 30px
    position: relative
    align-content: flex-start
    display: flex
    flex-wrap: wrap
    padding: 10px
    padding-top: 50px
    margin-top: 30px
    background-color: #2e2e2e
    width: 600px
    min-height: 330px
    height: max-content
    border-radius: 10px
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px
    h1
        position: absolute
        top: -10px
        left: 30px

@media screen and (max-width: 650px) 
    .stack-list
        width: 380px
        margin: 20px auto
        height: max-content
@media screen and (max-width: 425px)
    .stack-list
        width: 280px